/* Login.css */

.login-container {
    max-width: 400px;
    margin: 100px auto;
    background-color: #f7f7f7;
    padding: 2rem;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    text-align: center;
  }
  
  h2 {
    font-size: 1.8rem;
    margin-bottom: 1.5rem;
    color: #333;
  }
  
  .input-group {
    margin-bottom: 1rem;
    text-align: left;
  }
  
  .input-group input {
    width: 100%;
    padding: 0.75rem;
    font-size: 1rem;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    margin-top: 0.5rem;
  }
  
  .input-group input:focus {
    outline: none;
    border-color: #007bff;
    box-shadow: 0px 0px 8px rgba(0, 123, 255, 0.25);
  }
  
  .login-button {
    background-color: #007bff;
    color: white;
    padding: 0.75rem 1.5rem;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1rem;
    width: 100%;
    margin-top: 1rem;
  }
  
  .login-button:hover {
    background-color: #0056b3;
  }
  
  .forgot-password-button {
    background: none;
    border: none;
    color: #007bff;
    text-decoration: underline;
    cursor: pointer;
  }
  
  .forgot-password-button:hover {
    color: #0056b3;
  }
  
  .error-message {
    color: red;
    margin-bottom: 1rem;
  }
  
  .success-message {
    color: green;
    margin-bottom: 1rem;
  }
  
  /* Responsive Design */
  @media (max-width: 600px) {
    .login-container {
      padding: 1.5rem;
      margin: 50px auto;
    }
  }
  
  .contact-info {
    margin-top: 1.5rem;
    font-size: 0.9rem;
    color: #555;
  }
  
  .contact-info a {
    color: #007bff;
    text-decoration: none;
  }
  
  .contact-info a:hover {
    text-decoration: underline;
  }