/* ParentPortal.css */

.portal-container {
  padding: 2rem;
  text-align: center;
}

.child-data {
  border: 1px solid #ddd;
  padding: 1.5rem;
  margin: 1rem 0;
  border-radius: 5px;
}

.meeting-section {
  margin-top: 1rem;
}

.cta-button {
  background-color: #007bff;
  color: white;
  padding: 0.75rem 1.5rem;
  text-decoration: none;
  border-radius: 5px;
  font-size: 1rem;
}

.cta-button:hover {
  background-color: #0056b3;
}
