/* Navbar.css */

/* Basic navbar styling */
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #007bff; /* Primary background color */
    padding: 1rem 2rem;
    color: white;
  }
  
  .navbar-logo a {
    font-size: 1.5rem;
    font-weight: bold;
    color: white;
    text-decoration: none;
  }
  
  .navbar-links ul {
    list-style: none;
    display: flex;
    gap: 1.5rem;
  }
  
  .navbar-links ul li {
    padding: 0.5rem 0;
  }
  
  .navbar-links ul li a {
    color: white;
    text-decoration: none;
    font-size: 1rem;
  }
  
  .navbar-links ul li a:hover {
    border-bottom: 2px solid white;
    transition: 0.3s;
  }
  
  /* Style for logout button */
  .logout-button {
    background: none;
    border: none;
    color: white;
    font-size: 1rem;
    text-decoration: underline;
    cursor: pointer;
  }
  
  .logout-button:hover {
    border-bottom: 2px solid white;
    transition: 0.3s;
  }
  
  /* Responsive design for mobile */
  @media (max-width: 768px) {
    .navbar-links ul {
      flex-direction: column;
      display: none; /* Hide menu initially on mobile */
    }
  
    .show ul {
      display: flex; /* Show the menu when the 'show' class is added */
    }
  
    .hamburger {
      display: block;
      cursor: pointer;
      font-size: 2rem;
      color: white;
    }
  
    .navbar-logo {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
    }
  }
  